<template>
<div class="page yo-flex">
  <v-header title="单选" btn-txt="" :affirm="affirm">
    <a href="#" slot="regret" class="regret yo-ico ygh">&#xe604;</a>
  </v-header>
  <v-content>
    <v-list>
      <h2 class="label">单选</h2>
      <v-item>
          <div class="flex" @click="set1">类型：默认</div>
          <v-checked group="simple" :active="simple" val="single"></v-checked>
      </v-item>
      <v-item>
          <div class="flex" @click="set2">类型：默认</div>
          <v-checked group="simple" :active="simple" val="double"></v-checked>
      </v-item>
      <h2 class="label">单选 - 扩展边框</h2>
      <v-item>
          <div class="flex" @click="set3">类型：yo-checked-a</div>
          <v-checked group="border" :active="border" val="single" type="yo-checked-a"></v-checked>
      </v-item>
      <v-item>
          <div class="flex" @click="set4">类型：yo-checked-a</div>
          <v-checked group="border" :active="border" val="double" type="yo-checked-a"></v-checked>
      </v-item>
      <h2 class="label">单选 - 扩展背景</h2>
      <v-item>
          <div class="flex" @click="set5">类型：yo-checked-b</div>
          <v-checked group="back" :active="back" val="single" type="yo-checked-b"></v-checked>
      </v-item>
      <v-item>
          <div class="flex" @click="set6">类型：yo-checked-b</div>
          <v-checked group="back" :active="back" val="double" type="yo-checked-b"></v-checked>
      </v-item>
      <h2 class="label">单选 - 扩展圆角</h2>
      <v-item>
          <div class="flex" @click="set7">类型：yo-checked-c</div>
          <v-checked group="radius" :active="radius" val="single" type="yo-checked-c"></v-checked>
      </v-item>
      <v-item>
          <div class="flex" @click="set8">类型：yo-checked-c</div>
          <v-checked group="radius" :active="radius" val="double" type="yo-checked-c"></v-checked>
      </v-item>

    </v-list>
  </v-content>
  <v-footer>
    <template v-for="item in footer">
      <footer-item :icon="item.icon" :text="item.text" :active="item.active" :link="item.link"></footer-item>
    </template>
  </v-footer>
</div>
</template>

<script>
import Header from '../components/header/header';
import Content from '../components/content/index';
import Btn from '../components/btn/btn';
import Checked from '../components/checked/checked';

import {
  List,
  ListItem
}
from '../components/list/index';
import {
  Footer,
  FooterItem
} from '../components/footer/index';

export default {
  data() {
    return {
      msg: 'Welcome to use uikit',
      simple:'single',
      border:'single',
      back:'single',
      radius:'single',
      footer: [{
        icon: '&#xe680;',
        text:'主页',
        active: false,
        link: '/'
      }, {
        icon: '&#xe8bb;',
        text:'按钮',
        active: true,
        link: 'list'
      }, {
        icon: '&#xe637;',
        text:'滚动',
        active: false,
        link: 'scroll'
      }]
    }
  },
  methods:{
    affirm:function(){
      console.log(true);
    },
    btnClick:function(){
      alert('点到我了')
    },
    set1:function(){
      this.$data.simple='single'
    },
    set2:function(){
      this.$data.simple='double';
    },
    set3:function(){
      this.$data.border='single';
    },
    set4:function(){
      this.$data.border='double';
    },
    set5:function(){
      this.$data.back='single';
    },
    set6:function(){
      this.$data.back='double';
    },
    set7:function(){
      this.$data.radius='single';
    },
    set8:function(){
      this.$data.radius='double';
    }
  },
  components: {
    'v-header': Header,
    'v-content':Content,
    'v-list': List,
    'v-checked': Checked,
    'v-item': ListItem,
    'v-footer': Footer,
    'footer-item': FooterItem
  }
}
</script>



