<template lang="html">
  <div class="yo-checked" :class="type">
    <input type="radio" :name="group" :value="val" v-model="active">
    <span class="type"></span>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: ''
    },
    group: {
      type: String,
      default: 'project'
    },
    active: {
      type: String,
      default: 'single'
    },
    val:{
      type: String,
      default:''
    },
    click: {
      type: Function,
      default: () => {}
    }
  },
  methods:{
    _click(msg){
      if(this.click&&this.click instanceof Function){
        this.click.call(null,this);
      }
    }
  }
}
</script>



