<template lang="html">
  <button class="yo-btn yo-btn-s" @click="_click(this)">{{txt}}</button>
</template>

<script>
export default {
  props: {
    txt: {
      type: String,
      default: 'Button'
    },
    click: {
      type: Function,
      default: () => {}
    }
  },
  methods:{
    _click(msg){
      if(this.click&&this.click instanceof Function){
        this.click.call(null,this);
      }
    }
  }
}
</script>



